<template>
  <v-row justify="center">
    <v-col cols="12" sm="12" md="10" lg="8">
      <v-card>
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6">
                <v-responsive :aspect-ratio="4 / 3">
                  <div
                    ref="map-root"
                    v-bind:style="{ width: '100%', height: '100%' }"
                  ></div>
                </v-responsive>
              </v-col>
              <v-col cols="12" sm="6">
                <v-row>
                  <v-col>
                    <v-text-field
                    v-model=address
                      ref="address"
                      
                      :rules="[() => !!address || 'Campo requerido']"
                      label="Dirección de la obra"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col>
                    <v-text-field
                      ref="city"
                      prepend-icon="mdi-map-marker"
                      v-model="city"
                      :rules="[() => !!city || 'Campo requerido', addressCheck]"
                      label="Ciudad"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col
                    ><v-text-field
                      ref="state"
                      prepend-icon="mdi-map-marker"
                      v-model="state"
                      :rules="[() => !!state || 'Campo requerido']"
                      label="Provincia"
                      required
                    ></v-text-field
                  ></v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import View from "ol/View";
import Feature from "ol/Feature";
import Map from "ol/Map";
import Point from "ol/geom/Point";
// import OSM from "ol/source/OSM";
import XYZ from "ol/source/XYZ";
import GeoJSON from "ol/format/GeoJSON";
import * as geojsonObject from "./../assets/iso.json";
import { Vector as VectorSource } from "ol/source";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import { fromLonLat } from "ol/proj";
import { Fill, Stroke, Style } from "ol/style";
import { Icon } from "ol/style";


import "ol/ol.css";
import image from "./../assets/toy-brick-marker-outline.svg";

export default {
  name: "MapContainer",
  components: {  },
  props: ["title"],
  mounted() {
    // this is where we create the OpenLayers map
    this.olMap = new Map({
      // the map will be created using the 'map-root' ref
      target: this.$refs["map-root"],
      layers: [
        // adding a background tiled layer
        // new TileLayer({
        //   source: new OSM(), // tiles are served by OpenStreetMap
        // }),
        new TileLayer({
          source: new XYZ({
            url: "https://mt0.google.com/vt/lyrs=m&hl=en&x={x}&y={y}&z={z}",
          }),
        }),
      ],

      // the map view will initially show the whole world
      view: new View({
        zoom: 10,
        center: [-6500969.255654, -4111089.12929],
      }),
    });

    this.olMap.on("click", (evt) => {
      this.changeValue(evt.coordinate);
    });

    const vectorSource = new VectorSource({
      features: new GeoJSON().readFeatures(geojsonObject),
    });

    // vectorSource.addFeature(new Feature(new Circle([5e6, 7e6], 1e6)));
    let setStyleIso = (feature) => {
      const colors = {
        15: "rgba(69, 150, 230, 0)",
        30: "rgba(127, 172, 217, 0.1)",
        45: "rgba(184, 194, 204, 0.25)",
        60: "rgba(162, 159, 152, 0.5)",
        else: "rgba(118, 112, 100, 0.9)",
      };

      const widths = {
        15: 0.25,
        30: 0.25,
        45: 0.25,
        60: 0,
        else: 0,
      };

      const mins = feature.get("AA_MINS");

      const fill = new Fill({
        color:
          Object.keys(colors).indexOf(mins.toString()) !== -1
            ? colors[mins]
            : colors["else"],
      });
      const stroke = new Stroke({
        color: "#3399CC",
        width:
          Object.keys(widths).indexOf(mins.toString()) !== -1
            ? widths[mins]
            : widths["else"],
      });
      return new Style({
        fill: fill,
        stroke: stroke,
      });
    };

    const vectorLayer = new VectorLayer({
      source: vectorSource,
      style: setStyleIso,
    });

    this.olMap.addLayer(vectorLayer);

    const fabrica = new Feature({
      geometry: new Point(
        fromLonLat([-58.390167520719636, -34.69180373253449])
      ),
    });

    fabrica.setStyle(
      new Style({
        image: new Icon({
          color: "#8959A8",
          imgSize: [24, 24],
          src: image,
        }),
      })
    );

    this.olMap.addLayer(
      new VectorLayer({
        source: new VectorSource({
          features: [fabrica],
        }),
      })
    );
  },
  methods: {
    changeValue(value) {
      this.$emit("changeValue", JSON.stringify(value));
    },
    addressCheck() {
      this.errorMessages =
        this.address && !this.name ? `Hey! I'm required` : "";

      return true;
    },
  },
  data: () => ({
    errorMessages: "",
    address: null,
    city: null,
    state: null,
    zip: null,
    place: null,
    myHereApiKey: "API_KEY",
  }),
};
</script>
