<template>
  <v-row justify="center">
    <v-col cols="12" sm="12" md="10" lg="8">
      <v-card ref="form">
        <v-card-title>{{ title }}</v-card-title>
        <v-card-text>
          <v-card class="mx-auto" max-width="300" tile>
            <v-list autofocus>
              <v-list-item-group v-model="selectedItem" color="primary">
                <v-list-item
                  v-for="(option, n) in isValidOption"
                  :key="'SecondStepItem' + n"
                  @click="[changeValue(option.name)]"
                >
                  <v-list-item-icon>
                    <v-icon>{{ option.icon }}</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>{{ option.name }}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "child-second",
  props: ["prevValue", "options", "title"],
  data() {
    return {
      selectedItem: null,
    };
  },
  methods: {
    changeValue(value) {
      this.$emit("changeValue", value);
    },
  },
  computed: {
    isValidOption: function () {
      return this.options.filter((i) => i.prevValue === this.prevValue);
    },
  },
};
</script>
