<template>
  <v-row justify="center">
    <v-col cols="12" sm="10" md="8" lg="6">
      <v-data-table
        :headers="headers"
        :items="desserts"
        sort-by="calories"
        class="elevation-1"
        hide-default-footer
      >
        <template v-slot:[`top`]>
          <v-toolbar flat>
            <v-toolbar-title>{{ title }}</v-toolbar-title>

            <v-spacer></v-spacer>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on, attrs }">
                <v-badge :color="desserts.length == 0 ? 'error' : ''" overlap>
                  <v-btn
                    color="primary"
                    dark
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Añadir
                  </v-btn>
                </v-badge>
              </template>
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12" sm="6" md="4">
                        <v-select
                          :items="getProductsNames()"
                          v-model="editedItem.name"
                          @change="changeProduct(editedItem.name)"
                          :disabled="editedIndex !== -1"
                          label="Producto"
                          autofocus
                          dense
                        ></v-select>
                      </v-col>
                      <v-col cols="12" sm="6" md="4">
                        <v-select
                          :disabled="
                            editedItem.name == '' || editedIndex !== -1
                          "
                          :items="getProductsUnits(editedItem.name)"
                          label="Unidad"
                          @change="
                            changeProduct(editedItem.name, editedItem.unit)
                          "
                          v-model="editedItem.unit"
                          dense
                        ></v-select> </v-col
                    ></v-row>
                    <v-row>
                      <v-col cols="12">
                        <v-slider
                          :disabled="editedItem.name == ''"
                          v-model="editedItem[editedItem.unit]"
                          :label="editedItem.unit"
                          :max="255"
                          class="align-center"
                          @change="
                            changeProductAmount(
                              editedItem[editedItem.unit],
                              editedItem.unit
                            )
                          "
                        >
                          <template v-slot:append>
                            <v-text-field
                              v-model="editedItem[editedItem.unit]"
                              class="mt-0 pt-0"
                              type="number"
                              style="width: 60px"
                            ></v-text-field>
                          </template>
                        </v-slider>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="save"
                    :disabled="editedItem.name == ''"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Seguro que quieres borrar el resgistro?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancelar</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "Nombre",
        align: "start",
        sortable: false,
        value: "name",
      },
      { text: "Cantidad", value: "amount", sortable: false },
      { text: "Acciones", value: "actions", sortable: false },
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      amount: "",
      unit: "",
    },
    defaultItem: {
      name: "",
      amount: "",
      unit: "",
    },
  }),
  props: ["deserts", "products", "active", "title"],
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Añadir producto al pedido"
        : "Editar producto";
    },
  },

  watch: {
    active(val) {
      if (this.deserts == undefined || this.deserts.length == 0) {
        this.dialog = val;
      }
    },
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    changeProductAmount(amount, unit) {
      this.editedItem.amount = amount + " " + unit;
    },
    changeProduct(name, unit) {
      this.editedItem = Object.assign({}, this.defaultItem);
      this.editedItem.name = name;
      if (unit) {
        this.editedItem.unit = unit;
      } else {
        let list_units = this.getProductsUnits(name);
        if (list_units.length > 0) {
          this.editedItem.unit = list_units[0];
        }
      }
      this.editedIndex = -1;
    },
    getProductsUnits(name) {
      let ar = [];

      this.products.forEach((element) => {
        if (element.name == name) {
          ar = element.units;
        }
      });

      if (ar.length == 0) {
        return ["sacos", "kgs", "m3"];
      } else {
        return ar;
      }
    },
    getProductsNames() {
      let ar = [];
      this.products.forEach((element) => {
        ar.push(element.name);
      });
      return ar;
    },
    changeValue(value) {
      let ar = [];
      value.forEach((element) => {
        let property = element["name"];
        let obj = {};
        obj[property] = element["amount"];
        ar.push(obj);
      });
      this.$emit("changeValue", ar);
    },
    initialize() {
      this.desserts = [];
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.desserts.splice(this.editedIndex, 1);
      this.changeValue(this.desserts);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        this.desserts.push(this.editedItem);
      }
      this.changeValue(this.desserts);
      this.close();
    },
  },
};
</script>
