<style scoped>
.v-stepper--is-booted .v-stepper__content,
.v-stepper--is-booted .v-stepper__wrapper {
  transition: 1s cubic-bezier(0.25, 0.8, 0.5, 1);
}
</style>

<template>
  <v-stepper v-model="curr">
    <v-stepper-header class="overflow-x-auto">
      <template v-for="(step, n) in steps">
        <v-stepper-step
          :key="`${n}-step`"
          :complete="stepComplete(n + 1)"
          :step="n + 1"
          :rules="[(value) => !!step.valid]"
          :color="stepStatus(n + 1)"
        >
          {{ step.name }}

          <small v-if="step.optional">Opcional</small>
        </v-stepper-step>

        <v-divider v-bind:key="n" v-if="n < steps.length - 1" />
      </template>
    </v-stepper-header>
    <v-stepper-items>
      <v-stepper-content
        v-for="(step, n) in steps"
        :step="n + 1"
        :key="'StepContent' + n"
      >
        <FirstStep
          v-if="n == 0"
          :title="step.name"
          @changeValue="[(step.value = $event), (curr = n + 2)]"
          :options="step.options"
        />
        <SecondStep
          v-if="n == 1"
          :title="step.name"
          @changeValue="[(step.value = $event), (curr = n + 2)]"
          :prevValue="steps[0].value"
          :options="step.options"
        />
        <ThirdStep
          v-if="n == 2"
          :title="step.name"
          :products="step.products"
          :active="curr == n + 1"
          @changeValue="step.value = JSON.stringify($event)"
        />
        <FourStep
          v-if="n == 3"
          :title="step.name"
          @changeValue="step.value = $event"
        />

        <FiveStep
          v-if="n == 4"
          :contact="step.value"
          :title="step.name"
          @changeValue="step.value = $event"
          @changeValid="step.valid = $event"
        />

        <div class="d-none">
          <v-form :ref="'stepForm'" v-model="step.valid" lazy-validation>
            <v-text-field
              v-bind:value="step.value"
              :rules="step.rules"
            ></v-text-field>
          </v-form>
        </div>
        <v-btn
          v-if="n + 1 < lastStep"
          color="primary"
          @click="validate(n)"
          :disabled="!step.valid"
          >Continuar</v-btn
        >
        <v-btn v-else color="success" @click="[done(), submit(step)]"
          >Enviar</v-btn
        >
    

        <v-btn text @click="curr = n">Volver</v-btn>
      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import FirstStep from "./FirstStep.vue";
import SecondStep from "./SecondStep.vue";
import ThirdStep from "./ThirdStep.vue";
import FourStep from "./FourStep.vue";
import FiveStep from "./FiveStep.vue";

export default {
  data: () => ({
    dialogEnd:false,
    curr: 1,
    lastStep: 5,
    steps: [
      {
        name: "Tipo de obra",
        rules: [(v) => !!v || "Requerido."],
        value: "",
        valid: true,
        options: [
          { name: "Obra adjudicada", icon: "mdi-account-hard-hat" },
          { name: "Obra en estudio", icon: "mdi-calendar-search" },
        ],
      },
      {
        name: "Tipo de proyecto",
        rules: [(v) => !!v || "Requerido."],
        value: "",
        valid: true,
        options: [
          {
            prevValue: "Obra adjudicada",
            name: "Ejecución",
            icon: "mdi-office-building-cog",
          },
          {
            prevValue: "Obra adjudicada",
            name: "Explotación / Mantenimiento",
            icon: "mdi-office-building",
          },
          {
            prevValue: "Obra en estudio",
            name: "Estudio previo",
            icon: "mdi-chair-rolling",
          },
          {
            prevValue: "Obra en estudio",
            name: "Anteproyecto",
            icon: "mdi-chair-rolling",
          },
          {
            prevValue: "Obra en estudio",
            name: "Redacción de proyecto",
            icon: "mdi-chair-rolling",
          },
          {
            prevValue: "Obra en estudio",
            name: "Licitación",
            icon: "mdi-chair-rolling",
          },
        ],
      },
      {
        name: "Productos",
        rules: [(v) => (v && v.length >= 4) || "Enter at least 4 characters."],
        value: "",
        valid: true,
        products: [
          { name: "Hormigon", units: ["sacos", "kgs", "m3"] },
          { name: "Cemento", units: ["sacos", "kgs"] },
          { name: "Aridos", units: ["sacos", "kgs"] },
          { name: "Mortero", units: ["sacos", "kgs"] },
        ],
      },
      { name: "Dirección de la obra", valid: true, value: "", optional: true },
      {
        name: "Contacto",
        valid: true,
        value: {
          name: "",
          lastname: "",
          phone: "",
          date: "",
          email: "",
          company: "",
          notes: "",
        },
        rules: [
          (v) =>
            (v && v.name != "" && v.phone != "" && v.email != "") ||
            "Enter at least 4 characters.",
        ],
      },
    ],
    valid: false,
    stepForm: [],
  }),
  methods: {
    submit(step) {
      if (step.valid == false) {
        console.log("no puedo enviar formulario");
        return false;
      }
      this.formHasErrors = false;

      const scriptURL =
        "https://script.google.com/macros/s/AKfycbzfhVIHoIHqvyor8DlDtlEAoHAuUggah3gAYwMz8gMaK8j6b8e6vMVt65F1_VjfNGi7/exec";

      let formData = new FormData();
      this.steps.forEach((step) => {
        if (typeof step.value === "object") {
          for (let x in step.value) {
            formData.append(x, step.value[x]);
          }
        } else {
          formData.append(step.name, step.value);
        }
      });
this.dialogEnd=true;
      fetch(scriptURL, { method: "POST", body: formData })
        .then((response) => console.log("Success!", response))
        .catch((error) => console.error("Error!", error.message));
    },
    stepComplete(step) {
      return this.curr > step;
    },
    stepStatus(step) {
      return this.curr > step ? "green" : "blue";
    },
    validate(n) {
      this.steps[n].valid = false;
      let v = this.$refs.stepForm[n].validate();
      if (v) {
        this.steps[n].valid = true;
        this.curr = n + 2;
      }
    },
    done() {
      this.curr = 5;
    },
  },
  components: { FirstStep, SecondStep, ThirdStep, FourStep, FiveStep },
};
</script>
